.head {
    background-image: url(/Wallpapersz/static/media/Bg1.94ec1d04.JPG);
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
    min-height: 60vh;
    height: 90vh;
    display: flex;
    justify-content: center;
    background-blend-mode: overlay;
    align-items: center;
}

.head1 {
    text-align: center;
    color: blue;
    text-justify: auto;
    font-family: 'Courgette', cursive;
    font-size: 60px;
}
button {
    
    border: 1px solid transparent;
    outline: none;
    background-color: black;
    color: white;
    padding: 10px 25px;
    font-weight: bold;
    letter-spacing: 1.5px;
    border-radius: 5px;
    cursor: pointer;
}

.a{
    text-decoration: none;
}


button:hover{
    border-color: black;
    background-color: aliceblue;
    color: black;
}


